:root {
  --font-color: #333;
  --background-color: rgb(255, 255, 255);
  --link-color: cornflowerblue;
  --bgm: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 30 30'%3e%3cpath stroke='rgba%280, 0, 0, 1%29' stroke-linecap='round' stroke-miterlimit='10' stroke-width='2' d='M4 7h22M4 15h22M4 23h22'/%3e%3c/svg%3e");


}

[data-theme="dark"] {
  --font-color: #eee;
  --background-color: rgb(46, 46, 46);
  --link-color: lightblue;
  --bgm: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 30 30'%3e%3cpath stroke='rgba%28255, 255, 255, 4.55%29' stroke-linecap='round' stroke-miterlimit='10' stroke-width='2' d='M4 7h22M4 15h22M4 23h22'/%3e%3c/svg%3e");


}

body {
  background-color: var(--background-color);
  color: var(--font-color);
  transition: 1s;
}

a {
  color: var(--link-color);
}
