.toggle-theme-wrapper {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.toggle-theme-wrapper span {
  font-size: 20px;
  margin: 0 4px;
  position: flex;
}

.toggle-theme {
  position: relative;
  display: inline-block;
  height: 24px;
  width: 40px;
}

.toggle-theme input {
  display: none;
}

.slider {
  background-color: #ccc;
  position: absolute;
  cursor: pointer;
  bottom: 0;
  left: 0;
  right: 0;
  top: 0;
  transition: 2s;
}

.slider:before {
  background-color: var(--font-color);
  bottom: 4px;
  content: "";
  height: 15px;
  left: 1px;
  position: absolute;
  transition: 0.4s;
  width: 15px;
}

input:checked + .slider:before {
  transform: translateX(22px);
}

input:checked + .slider {
  background-color: rgb(68, 79, 80);
}

.slider.round {
  border-radius: 24px;
}

.slider.round:before {
  border-radius: 50%;
}
